export default {
	data() {
		return {
			// tabs 栏数据
			tabs: [
				{
					id: 0,
					text: '绿色健康'
				},
				{
					id: 1,
					text: '电商助农'
				}
			],
			// 默认选中的那一项
			current: 0,
			// 购物车数量
			num: 1,
			// 控制有消息页面和无消息页面的切换
			isBox: true
		}
	},
	methods: {
		// 点击 tabs 更改数据
		changeText(index) {
			this.current = index
		},
		// 点击加号
		plus() {
			this.num++
		},
		// 点击减号
		reduce() {
			if (this.num > 1) {
				this.num--
			}
		},
		// input 输入的内容
		input(e) {
			let result = e.detail.value
			if (result !== '' || result !== 0) {
				this.num = result
			}
		},
		// 输入框失去焦点
		blur(e) {
			if (e.detail.value === '' || e.detail.value == 0) {
				this.num = 1
			}
		},
		// 去首页
		goIndex() {
			uni.switchTab({
				url: '/pages/index/index'
			})
		}
	},
	// 控制减号的显示以及输入的范围
	computed: {
		isColor() {
			if (this.num <= 1) {
				return true
			}
			return false
		}
	}
}
